.contact-wrapper {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    padding-top: 45px;
    padding-bottom: 60px;
    overflow: scroll;
}

.contact {
    height: 100vh;    
}

.contact-info {
    width: 50%;    
}

@media(max-width: 1150px) {
    .contact-info {
        width: 70%;
    }
}

@media(max-width: 600px) {
    .contact-info {
        width: 90%;
    }
}

.contact-header {
    text-align: center;
    padding: 15px;
}

.actual-contact {
    color: #CF5C36;
    font-weight: 700;
    font-size: 40px;
}

.question {
    font-weight: 400;
    color: rgb(52, 163, 148);
}

.contact-form {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    margin: 30px auto;
}

.contact-form > * {
    margin: 10px 0px;
    font-size: 15px;
    color: rgb(34, 34, 34);
    padding-left: 10px;
    background-color: rgb(236, 236, 236);
    border: none;
    border-radius: 5px;
    -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
            box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.contact-form > *:focus {
    outline: none;
}

.contact-inputs {
    height: 30px;
}

#message {
    resize: none;
}

#submit {
    width: 150px;
    height: 30px;
    margin: 10px auto;
    border-radius: 5px;
    background-color: #CF5C36;
    color: white;
    cursor: pointer;
}




